
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sticky from 'vux/src/components/sticky/index.vue'

export default {
name: 'voHeader',
data() {
return {
index: 0,
list: ['报名', '科目一', '科目二', '科目三', '领证'],
selected: '报名'
}
},
components: {
Tab,
TabItem,
Sticky
},
mounted() {
},
methods: {
onMenuChange(item){
console.log(item)
this.selected = item
if(item === '报名'){
this.$router.push({name: 'Home'})
}else{
this.$router.push({name: 'Coming'})
}
}
}
}



