
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Rater from 'vux/src/components/rater/index.vue'

import { getJsSdk } from '@/service/getData'
const list = () => ['精选', '美食', '电影', '酒店', '外卖']

export default {
components: {
Card,
Group,
Cell,
CellFormPreview,
Rater
},
data() {
return {
raterData: 5
}
},
mounted(){
this.initWX()
},
computed:{
raterDataDisplay(){
return '5.0'
}
},
methods: {
gotoDetial(){
this.$router.push({name: 'Detail'})
},
async initWX(){
var wxConfig = await getJsSdk({
url: window.location.href.split('#')[0]
})
var wx = this.$wechat;
console.log(1)
console.log(wxConfig)
wx.config({
debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
appId: wxConfig.appId, // 必填，公众号的唯一标识
timestamp: wxConfig.timestamp, // 必填，生成签名的时间戳
nonceStr: wxConfig.nonceStr, // 必填，生成签名的随机串
signature: wxConfig.signature, // 必填，签名，见附录1
jsApiList: wxConfig.jsApiList 
})
wx.ready(function () {
wx.checkJsApi({
jsApiList: ['chooseImage'], // 需要检测的JS接口列表，所有JS接口列表见附录2,
success: function(res) {
console.log(res)
}
});
wx.onMenuShareTimeline({
title: '易佳驾让您无忧学车，快速拿证！',
desc: '点击进入有好礼相送！',
link: window.location.href,
imgUrl: 'http://yy.machsys.cn/static/road.jpg'
});
wx.onMenuShareAppMessage({
title: '易佳驾让您无忧学车，快速拿证！',
desc: '点击进入有好礼相送！',
link: window.location.href,
imgUrl: 'http://yy.machsys.cn/static/road.jpg'
});
})

wx.error(function (res) {
console.log(res)

})
},
}
}



